import { Chart } from "primereact/chart";
import React, { useEffect, useState } from "react";
import { PriceServices } from "../../services/priceServices";
import { Button } from "primereact/button";
import { useHistory } from "react-router";

const priceServices = PriceServices;
export default function GuessingChartSeller() {
  const routing = useHistory();
  const [valueDaNang, setValueDaNang] = useState([]);
  const [valueHoChiMinh, setValueHoChiMinh] = useState([]);
  const [valueHaNoi, setValueHaNoi] = useState([]);

  const basicData = {
    labels: [
      "10/05/2022",
      "17/05/2022",
      "24/05/2022",
      "30/05/2022",
      "7/06/2022",
      "Hin ti",
    ],
    datasets: [
      {
        label: " Nng",
        data: valueDaNang,
        fill: false,
        borderColor: "#42A5F5",
        tension: 0.4,
      },
      {
        label: "H Ch Minh",
        data: valueHoChiMinh,
        fill: false,
        borderColor: "#FFA726",
        tension: 0.4,
      },
      {
        label: "H Ni",
        data: valueHaNoi,
        fill: false,
        borderColor: "#FF6384",
        tension: 0.4,
      },
    ],
  };
  let basicOptions = {
    maintainAspectRatio: false,
    aspectRatio: 0.6,
    plugins: {
      legend: {
        labels: {
          color: "#495057",
        },
      },
    },
    scales: {
      x: {
        ticks: {
          color: "#495057",
        },
        grid: {
          color: "#ebedef",
        },
      },
      y: {
        ticks: {
          color: "#495057",
        },
        grid: {
          color: "#ebedef",
        },
      },
    },
  };

  useEffect(() => {
    priceServices.getSellerPriceByProvince(" Nng").then((res) => {
      setValueDaNang(res);
    });
    priceServices.getSellerPriceByProvince("H Ch Minh").then((res) => {
      setValueHoChiMinh(res);
    });
    priceServices.getSellerPriceByProvince("H Ni").then((res) => {
      setValueHaNoi(res);
    });
  }, []);

  return (
    <div className="guessing-chart">
      <div
        className="view-chart-title"
        style={{ fontSize: "20px", textTransform: "uppercase" }}
      >
        Thng tin biu  v tng trng bt ng sn (triu / m2)
      </div>
      <div className="text-align-center mb-3">
        <Button
          onClick={() =>
            routing.push({
              pathname: "/detailed/guessing-seller-chart",
              state: { province: "H Ni" },
            })
          }
          label="Xem thng tin chi tit thnh ph H Ni"
        />
        <Button
          onClick={() =>
            routing.push({
              pathname: "/detailed/guessing-seller-chart",
              state: { province: " Nng" },
            })
          }
          className="mr-4 ml-4"
          label="Xem thng tin chi tit thnh ph  Nng"
        />
        <Button
          onClick={() =>
            routing.push({
              pathname: "/detailed/guessing-seller-chart",
              state: { province: "H Ch Minh" },
            })
          }
          label="Xem thng tin chi tit thnh ph H Ch Minh"
        />
      </div>
      <div className="chart-center">
        <Chart
          type="line"
          data={basicData}
          options={basicOptions}
          height="600px"
          width="1200px"
        />
      </div>
    </div>
  );
}
